h1, h2, h3 {
  margin-top: 0;
}

a {
  display: block;
}

p a {
  display: inline;
}

li {
  list-style-type: none;
}

h1, h2, h3, h4, h5, h6,
p, span, a, strong, blockquote,
i, b, u, em {
  font-size: 1em;
  font-weight: inherit;
  font-style: inherit;
  text-decoration: none;
  color: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    animation: none;
    transition: none;
  }
}
*, *::before, *::after {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  vertical-align: baseline;
}

img,
picture,
video,
iframe,
figure {
  max-width: 100%;
  width: 100%;
  display: block;
  object-fit: cover;
  object-position: center center;
}

html {
  scroll-behavior: smooth;
}

table, tr, td {
  border-collapse: collapse;
  border-spacing: 0;
}

svg {
  width: 100%;
  display: block;
  fill: currentColor;
}

body {
  max-width: 100%;
  min-height: 100vh;
  font-size: 100%;
  font-family: var(--font-family);
  color: var(--color-text);
  background: var(--bg);
  line-height: 1.4em;
  font-smooth: always;
  --webkit-font-smoothing: antialiased;
  --moz-osx-font-smoothing: grayscale;
  transition: 0.5s ease-in-out;
  position: relative;
}

.artistic-text {
  font-family: var(--font-family-2);
  background: linear-gradient(var(--main-color) 35%, var(--bg) 88%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.artistic-text--responsive {
  font-size: 16vw;
  line-height: 100%;
}
@media (min-width: 40em) {
  .artistic-text--responsive {
    font-size: 5rem;
  }
}
@media (min-width: 64em) {
  .artistic-text--responsive {
    font-size: 7.5rem;
  }
}

.main-container {
  min-width: 23.4375rem;
  max-width: 87.5rem;
  padding: 0;
  margin: 0 auto;
}

.container {
  min-width: 23.4375rem;
  margin: 0 auto 2.8125rem;
}
@media (min-width: 64em) {
  .container {
    padding: 0 4rem;
  }
}

.card {
  padding: 1.25rem;
}

.toggle__button {
  border-radius: 50%;
  width: 1rem;
  height: 0.5rem;
  background-color: var(--card-hover);
}