@use '../util/' as *;

h1,h2,h3{
  margin-top: 0;
}

a {display: block;}

p a {display: inline;}

li {list-style-type: none;}

h1,h2,h3,h4,h5,h6,
p,span,a,strong,blockquote,
i,b,u,em {
  font-size: 1em;
  font-weight: inherit;
  font-style: inherit;
  text-decoration: none;
  color: inherit;
}